@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{
background: #544a7d;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #ffd452, #544a7d); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
padding: 4em;
color: aliceblue;
text-align: center;
}




header img {
    padding-bottom: 1em;
    height: 250px;
    width: 270px;
    transition: .8s ease-in-out;
} 

header img:hover {
    transform: rotate(360deg) scale(1.5);
}

header h1 {
    padding-top: 0.5em;
    font-family: 'Pacifico';
    text-shadow: black;
}
footer{
background: burlywood;
color: black;
text-align: center;
font-style: bold;
}


.List-container{
margin: 2em ;
}

h2.List-header{
text-align: center ;    
font-family: 'Balsamiq sans';
font-weight: 900px;
background-color: rgb(175, 81, 230);
color: cornsilk;
border-radius: 10px;
font-size: 2.5 em;
padding: 0.5em;

}

.List-input{
    padding: 1em;
    width: 100%;
    border-radius: 10px;
    font-size: 20px;
    font-family: 'Balsamiq sans';
    margin-bottom: 10px;
    margin-top: 10px;
    
}

.submit-button{
    height: 70px;
    margin-top: 10px;
    padding: 1em;
    background-color: blueviolet;
}


.List-item{
    background-color: rgb(250, 222, 152);
    color: black;
    padding: 1em;
    font-size: 20px;
    border-radius: 10px;
    font-family: 'Balsamiq sans';
    margin-top: 10px;
}

.List-item:hover {
    transform: scale(1.02)
}

.List-item input[type = checkbox]{
padding: 0.3em;
transform : scale(1.8)
}


